const CmdBlock: React.FunctionComponent<any> = (props: any) => {
  const [isOpen, setIsOpen] = useState(false);

  let syntaxElems = [];
  for (let arg of props.cmd.syntax.slice(1)) {
    syntaxElems.push(<><span> </span><span style={{color: '#08596E'}}>[{arg}]</span></>);
  }

  return (
    <div className="card cmdBlock">
      <div className="card-body">
        <div className="cmd-heading" onClick={() => setIsOpen(!isOpen)}>
          <div className='row'>
            <div className='col-sm-10 col-md-11'>
            <h5 className="card-title">
            <span
              style={{
                color:
                  props.cmd.color === undefined ? "white" : props.cmd.color,
              }}
            >
              {props.cmd.cmd}
            </span>
          </h5>
          <h6> - {props.cmd.shortDesc}</h6>
            </div>
            <div className='col-sm-2 col-md-1' style={{textAlign: 'right'}}>
            <span style={{color: 'white', fontSize: 20}}>{isOpen ? arrUp : arrDown}</span>
              
            </div>
          </div>
          

        </div>
        {props.cmd.tags.includes("price") && (
          <span className="badge badge-primary">Price</span>
        )}
        <span> </span>
        {props.cmd.tags.includes("alarms") && (
          <span className="badge badge-warning">Alarms</span>
        )}
        <span> </span>
        {props.cmd.tags.includes("premium") && (
          <span className="badge badge-success">Premium {donorStar}</span>
        )}
        <span> </span>
        {props.cmd.tags.includes("config") && (
          <span className="badge badge-light">Config</span>
        )}

        <Collapse isOpened={isOpen}>
          <hr />
          <div className="blockMain">
            <div className="card">
              <div className="card-body" style={{ padding: 5 }}>
                <h5 className="card-title" style={{ color: "#2E2E2E" }}>
                  Usage
                </h5>
        <p className="card-text"><span style={{color: 'black'}}>{props.cmd.syntax[0]}</span> {syntaxElems}</p>
              </div>
            </div>
            <div className="card">
              <div className="card-body" style={{ padding: 5 }}>
                <h5 className="card-title" style={{ color: "#2E2E2E" }}>
                  Example
                </h5>
                <p className="card-text"><span style={{color: 'black'}}>{props.cmd.example}</span></p>
              </div>
            </div>
          </div>
        </Collapse>
      </div>
    </div>
  );
};

function App() {
  const [searchStr, setSearchStr] = useState("");
  const searchStrLower = searchStr.toLowerCase();

  const [activeTag, setActiveTag] = useState("all");

  const renderCmds = () => {
    const blocks = [];

    for (const cmd of cmds) {
      const masterTagFilterIsValid =
        activeTag === "all" || cmd.tags.includes(activeTag);
      if (!masterTagFilterIsValid) continue;

      let containsTag = false;
      for (let tag of cmd.tags) {
        if (tag.includes(searchStrLower)) {
          containsTag = true;
          break;
        }
      }

      if (cmd.cmd.toLowerCase().includes(searchStrLower) || containsTag)
        blocks.push(<CmdBlock cmd={cmd} key={"cmd-" + cmd.cmd} />);
    }
    return blocks;
  };

  const renderTags = () => {
    const elems = [];
    for (const tag of tagFilters) {
      elems.push(
        <>
          <a
            onClick={() => {
              setActiveTag(tag);
            }}
            className={
              (activeTag === tag
                ? "tagSelector-active"
                : "tagSelector-inactive") + " tagSelector"
            }
            key={"tag-" + tag}
          >
            {tag} {tag === "premium" ? donorStar : undefined}
          </a>
          <span> </span>
        </>
      );
    }
    return elems;
  };

  return (
    <div className="App">
      <input
        className="searchInput"
        type="text"
        placeholder="Search for command ðŸ”"
        value={searchStr}
        onChange={(e) => {
          setSearchStr(e.target.value);
        }}
      />

      <br />

      <div>{renderTags()}</div>

      <br />

      {renderCmds()}
    </div>
  );
}

export default App;
